/* General App styles */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* New styles for centering the content and adding margin to the left */
.center-content {
  text-align: center;
  margin-bottom: 20px;
}

/* Top navigation styling */
.top-nav {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

/* Pulse Animation for Links */
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    color: #61dafb; /* Initial color */
  }
  50% {
    transform: scale(1.1); /* Slightly grow */
    color: #4ecdc4; /* Change color for a pulsing effect */
  }
}

/* Apply the pulse effect to each navigation link */
.nav-link {
  color: #61dafb; /* Base color */
  text-decoration: none;
  font-size: 1.2em;
  font-weight: bold;
  animation: pulse 2s infinite; /* Pulse effect */
  transition: transform 0.3s ease; /* Smooth transition */
}

.nav-link:hover {
  color: #ff6b6b; /* Hover color */
  transform: scale(1.15); /* Slightly larger on hover */
}

/* Add margin on the left for other sections */
.left-space {
  margin-left: 20px;
}

/* Specific image sizing for each section */
.academic-image {
  width: 300px;
  height: auto;
  margin-bottom: 20px;
}

.work-image {
  width: 300px;
  height: auto;
  margin-bottom: 20px;
}

.research-image {
  width: 400px;
  height: auto;
  margin-bottom: 20px;
}

.ai-image {
  width: 400px;
  height: auto;
  margin-bottom: 20px;
}

/* Consistent layout across each page */
.page-content {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
  text-align: left;
}
